import { createSlice } from '@reduxjs/toolkit';
const initialState = null;

export const filterSlice = createSlice({
  name: 'filter',
  initialState,
  reducers: {
    search: (state, action) => {
      return action.payload;
    },
  },
});

export const { search } = filterSlice.actions;

export default filterSlice.reducer;
